<template>
  <div>
    <div>
      <van-form @submit="onSubmit">
        <van-field
          v-model="person.username"
          name="username"
          label="您的姓名"
          placeholder="请输入您的姓名 (例: 陈先生)"
          AUTOCOMPLETE="off"
          :rules="[{ required: true, message: '名称不能为空!' }]"
        />
        <van-field
          v-model="person.tel"
          name="tel"
          label="联系电话"
          placeholder="请输入联系电话"
          AUTOCOMPLETE="off"
          :rules="[{ validator, message: '无效的手机号码!' }]"
        />
        <van-field
          v-model="person.addr"
          name="addr"
          label="卖货地址"
          placeholder="请输入卖货地址(例如：恩施市舞阳坝)"
          AUTOCOMPLETE="off"
          :rules="[{ required: false, message: '' }]"
        />
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit">
            提交
          </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
//引入Vue
import Vue from "vue";
//引入vant中的Toast
import { Toast } from "vant";
Vue.use(Toast);
//引入axios
import axios from "axios";
Vue.prototype.$axios = axios;
//notify
import { Notify } from "vant";
Vue.use(Notify);

export default {
  data() {
    return {
      person: {
        username: "",
        tel: "",
        addr: "",
      },
    };
  },
  methods: {
    // 正则匹配电话号码
    validator(val) {
      return /^1[3456789]\d{9}$/.test(val);
    },
    // 提交表单内容
    onSubmit() {
      //console.log("提交中" + JSON.stringify(this.person)); //提交的数据 this.person
      Toast.loading({
        message: "Loading...",
        forbidClick: true,
        loadingType: "spinner",
      });

      this.$axios
        .get("/addData", {
          params: {
            video_id: 4,
          },
        })
        .then((res) => {
          //提交成功, 清空提交信息
          for (const key in this.person) {
            this.person[key] = "";
          }

          Notify({
            type: "success",
            message: "提交成功!收购员正在赶来的路上",
          });
          console.log(res);
        })
        .catch((error) => {
          Notify({ type: "danger", message: "提交失败!" });
        });

      return false;
    },
  },
};
</script>

<style>
</style>


