<template>
  <div id="box-page">
    <!-- Enable scroll when text is short -->
    <van-notice-bar
      color="#1989fa"
      background="#ecf9ff"
      left-icon="volume-o"
      scrollable
      text="本平台承诺：凡交易成功,线下人员将额外支付用户 1 元奖励!"
    />
    <h2>{{ msg }}</h2>
    <!-- 主页按钮 -->
    <van-row>
      <van-row>
        <van-col span="14" offset="5" class="btm_pg_dt">
          <van-button type="primary" size="large" block @click="saleTab"
            >商品售卖</van-button
          >
        </van-col>
      </van-row>
      <van-row>
        <van-col span="14" offset="5">
          <van-button type="info" size="large" block @click="priceTab"
            >商品报价表</van-button
          >
        </van-col>
      </van-row>
    </van-row>
  </div>
</template>



<script>
import Vue from "vue";
import { NoticeBar } from "vant";
//引入router
import router from "@/router";

Vue.use(NoticeBar);

export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "新宇首页",
    };
  },
  components: {},
  methods: {
    saleTab() {
      router.replace({ path: "/InfoData" });
    },
    priceTab() {
      router.replace({ path: "/Login" });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  margin: 7rem 0;
  font-size: 3rem;
  text-align: center;
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

#box-page {
  height: 100%;
}

.btm_pg_dt {
  margin-bottom: 1rem;
}
</style>


