import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import InfoData from '@/components/InfoData'
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:"/InfoData",
      name:"InfoData",
      component:InfoData
    },
    {
      path:"/Login",
      name:"Login",
      component:Login
    }
  ]
})



