      text="本平台承诺：凡交易成功,线下人员将额外支付用户 1 元奖励!"
    />
  </div>
</template>

<script>
//引入router
import router from "@/router";
//注意栏
import Vue from "vue";
import { NoticeBar } from "vant";

Vue.use(NoticeBar);

export default {
  name: "Login",
  data() {
    return {

    };
  },
  methods:{
      onClickLeft () {
          router.replace({path:"/"})
      }
  }
};
</script>

