<template>
  <div>
    <van-nav-bar
      title="填写联系信息"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <!-- Enable scroll when text is short -->
    <van-notice-bar
      color="#1989fa"
      background="#ecf9ff"
      left-icon="volume-o"
      scrollable
      text="本平台承诺：凡交易成功,线下人员将额外支付用户 1 元奖励!"
    />
    <!-- 头部关于 -->
    <my-component></my-component>
  </div>
</template>

<script>
//引入子组件
import MyComponentVue from "./MyComponent";
//引入router
import router from "@/router";
//注意栏
import Vue from "vue";
import { NoticeBar } from "vant";

Vue.use(NoticeBar);
export default {
  name: "About",
  data() {
    return {
      msg: "这是关于界面",
    };
  },
  components: {
    "my-component": MyComponentVue,
  },
  methods: {
    //返回主页面
    onClickLeft() {
      router.replace({ path: "/" });
    },
  },
};
</script>

<style>
</style>


